import React, { Component } from 'react';

// material-ui
import { Card, Button } from 'react-materialize';

// style
import './SearchResults.css';

// api
import API from '../../utils/API';

export default class SearchResults extends Component {

    // save book to mongo db
    saveBook = () => {
        console.log('save');
        API.getSavedBooks()
            .then((response) => {
                console.log('API.getSavedBooks()')
                console.log(response);
            })
            .catch((err) => { console.log(`API ERROR: ${err}`) });

        // API.getSavedBooks().then(function (response) {
        //     console.log(response)
        // }).catch(function (err) {
        //     if (err) throw err;
        // })

    }

    render() {
        return (

            <div className="row mt-5">
                {/* map through array of objects and create card */}
                {
                    this.props.searchResults.map(book => (

                        // if wrapped in a container, will lose cascading card style
                        <Card className="container"
                            key={'card' + book.id}
                            actions={
                                [
                                    <Button key={'view' + book.id}><a href={book.volumeInfo.infoLink} target="_blank" rel="noopener noreferrer">View</a></Button>,
                                    <Button key={'save' + book.id} onClick={this.saveBook}>Save</Button>

                                ]
                            }
                        >
                            <div className="row">
                                <div className="col-md-3">
                                    <img src={book.volumeInfo.imageLinks.smallThumbnail} alt="" className="imgclass" />
                                </div>
                                <div className="col-md-9">
                                    <h6>{book.volumeInfo.title}</h6>
                                    <p>Author: {book.volumeInfo.authors[0]}</p>
                                    <p>Rating: {book.volumeInfo.averageRating ? book.volumeInfo.averageRating : 'No Rating'}</p>
                                    <p>Desc: {book.volumeInfo.description}</p>
                                </div>
                            </div>
                        </Card >

                    ))
                }



            </div>

        )
    }
}
